@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
.main{
    width: 100%;
    height: auto;
    background-color: #cbd5e1;
    padding-top: 50px;

}

.main-header {
    position: fixed;
    height: 40px;
    background-color: white;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: 2px 4px 5px 6px rgba(0,0,0,0.35);
    display: flex;
    align-items: center;
    column-gap: 10px;
}
.modal-setting{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: white;
    min-width: 500px;
    min-height: 500px;
    box-shadow: 2px 4px 5px 6px rgba(0,0,0,0.35);
}
.main-page{
    display: flex;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #cbd5e1;
}
.out-page{
    background-color: white;
    margin-top: 40px;
    margin-bottom: 20px;
    box-shadow: 2px 4px 5px 6px rgba(0,0,0,0.35);
}
.page{
    page-break-after: always;
    display: flex;
    flex-direction: column;
    height: 8.5in;
    width:  15in;
}
.page table {
    border-collapse: collapse;
    margin: auto;
    width: 95%;
}
.page table thead tr.headerColumn{
    border-bottom: 1px solid black;
}
.page table td,.page table th{
    font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-style: normal;
  padding-left: 5px;
  padding-right: 5px;
}

.content{
    flex: 1;
    block-size: fit-content;

}
.content img{
    height: 100%; 
    width: 95%; 
    object-fit: cover; 
}

.footer, .header{
    padding-left: 50px;
    padding-right: 50px;
}
label{
    font-family: "Roboto", sans-serif;
    font-size: 11px;
    font-weight: 500;
}